import {createRouter, createWebHashHistory} from "vue-router";
import ConnectVue from "@/components/ConnectVue.vue";
import App from "@/App";

const routes = [
    {
        path: "/",
        name: "Home",
        component: ConnectVue,
    },
    {
        path: "/details/:ip/:port",
        name: "Details",
        component: App, // Lazy-loaded component
    },
];

const router = createRouter({
    history: createWebHashHistory(),
    routes,
    mode: 'hash'
});

export default router;
