import { createStore } from "vuex";

export default createStore({
    state: {
        client: null, // WebSocket 客户端
        connections: [], // 已连接的远程服务
        datas: {}, // 数据缓存，以 "ip:port" 为键，存储最近 30 条数据
        deviceList: {}, // 当前可用设备列表
        currentdevice:{}
    },
    mutations: {
        SET_CLIENT(state, client) {
            state.client = client;
        },
        ADD_CONNECTION(state, connection) {
            state.connections.push(connection);
        },
        REMOVE_CONNECTION(state, connection) {
            state.connections = state.connections.filter(
                (conn) => conn.ip !== connection.ip || conn.port !== connection.port
            );
        },

        SET_DEVICES(state,{key, devices}) {
            state.deviceList[key] = devices;
        },
        SET_CURRENTDEVICE(state,{key,data}){
          state.currentdevice[key]=data;
        },
        ADD_DATAPACKET(state, { key, data }) {
            if (!state.datas[key]) {
                state.datas[key] = [];
            }
            // 确保队列大小不超过 30
            if (state.datas[key].length >= 30) {
                state.datas[key].shift(); // 移除最早的数据
            }
            state.datas[key].push(data); // 添加新数据
        },
        REMOVE_DEVICES(state,key){
            state.currentdevice[key]=null;
        }
    },
    actions: {
        async initConnect({ commit }) {
            const wsClient = new WebSocket(`${process.env.VUE_APP_BASE_WEBSOCKET}/capture/`);
            wsClient.onopen = () => {
                console.log("WebSocket connection established.");
                commit("SET_CLIENT", wsClient);
            };
            wsClient.onmessage = (event) => {
                const data = JSON.parse(event.data);
                console.log("Received from server:", data);
                switch (data.status) {
                    case "datapacket": {
                        const parsedPacket = JSON.parse(data.packet);
                        const ip = data.ip || "unknown";
                        // const port = parsedPacket.src_port || "unknown";
                        // const key = `${ip}:${port}`;
                        commit("ADD_DATAPACKET", { key:ip, data: parsedPacket });
                        break;
                    }
                    case "devicelist": {
                        const devices = JSON.parse(data.device);
                        commit("SET_DEVICES", {key:data.ip,devices:devices});
                        break;
                    }
                    case "success": {
                        console.log("Operation success:", data.message);
                        break;
                    }
                    case "successconnect":{
                        console.log("Connect ok",data.message);
                        break;
                    }
                    case "failconnected": {
                        console.log("Connection failed:", data);
                        commit("REMOVE_CONNECTION", { ip: data.ip, port: data.port });
                        break;
                    }
                    case "getstatus": {
                        console.log("Monitor device:", data.device)
                        commit("SET_CURRENTDEVICE",{key:data.ip,data:data.device})
                        break;
                    }
                    case "stopdevice":{
                        commit("REMOVE_DEVICES",data.ip)
                        console.log("Stop monitor device");
                        break;
                    }
                    default:
                        console.error("Unknown status received:", data.status);
                }
            };
            wsClient.onerror = (error) => {
                console.error("WebSocket error:", error);
            };
            wsClient.onclose = () => {
                console.log("WebSocket connection closed.");
                commit("SET_CLIENT", null);
            };
        },
        async connectRemoteServer({ state, commit }, params) {
            const { client } = state;
            if (!client || client.readyState !== WebSocket.OPEN) {
                console.error("WebSocket is not connected.");
                return;
            }
            const { ip, port } = params;
            const connectPayload = {
                command: "connect",
                ip,
                port,
            };
            client.send(JSON.stringify(connectPayload));
            commit("ADD_CONNECTION", connectPayload);
            console.log(`Sent connect request: ${JSON.stringify(connectPayload)}`);
        },
        // eslint-disable-next-line no-unused-vars
        async changeDevice({ state, commit }, { device, target }) {
            const { client, deviceList } = state;
            if (!client || client.readyState !== WebSocket.OPEN) {
                console.error("WebSocket is not connected.");
                return;
            }
            if (!deviceList[target.ip].includes(device)) {
                console.error("Invalid device:", device);
                return;
            }
            const switchPayload = {
                command: "send_command",
                action: "switch_device",
                device,
                ip: target.ip,
                port: target.port,
            };
            client.send(JSON.stringify(switchPayload));
            commit("SET_CURRENTDEVICE",{key:target.ip,data:device})
            console.log(`Sent switch device request: ${JSON.stringify(switchPayload)}`);
        },
        async stopCapture({ state }, target) {
            const { client } = state;

            if (!client || client.readyState !== WebSocket.OPEN) {
                console.error("WebSocket is not connected.");
                return;
            }
            const stopPayload = {
                command: "send_command",
                action: "stop",
                ip: target.ip,
                port: target.port,
            };
            client.send(JSON.stringify(stopPayload));
            console.log("Sent stop capture request.");
        },
        // eslint-disable-next-line no-unused-vars
        async getDevices({ state, commit }, target) {
            const { client } = state;
            if (!client || client.readyState !== WebSocket.OPEN) {
                console.error("WebSocket is not connected.");
                return;
            }
            const listPayload = {
                command: "send_command",
                action: "list_devices",
                ip: target.ip,
                port: target.port,
            };
            client.send(JSON.stringify(listPayload));
            console.log("Sent list devices request.");
        },
        async getRemoteStatus({state},target){
            const {client} =state;
            if (!client || client.readyState !== WebSocket.OPEN) {
                console.error("WebSocket is not connected.");
                return;
            }
            const datas={
                command:"send_command",
                action:"status",
                ip:target.ip,
                port:target.port
            };
            client.send(JSON.stringify(datas));
            console.log("send status")
        }
    },
});