<template>
  <div>
    <!-- 连接表单 -->
    <a-form
        :model="formState"
        name="connect_form"
        layout="inline"
        autocomplete="off"
        @finish="onSubmit"
        @finishFailed="onFinishFailed"
    >
      <a-form-item
          label="IP Address"
          name="ip"
          :rules="[{ required: true, message: 'Please input the IP address!' }]"
      >
        <a-input v-model:value="formState.ip" placeholder="Enter IP" />
      </a-form-item>
      <a-form-item
          label="Port"
          name="port"
          :rules="[{ required: true, message: 'Please input the port!' }]"
      >
        <a-input-number
            v-model:value="formState.port"
            :min="1"
            :max="65535"
            placeholder="Enter Port"
        />
      </a-form-item>
      <a-form-item>
        <a-button type="primary" html-type="submit">
          Connect
        </a-button>
      </a-form-item>
    </a-form>

    <!-- 已连接服务列表 -->
    <div style="margin-top: 20px;">
      <h3>Connected Servers</h3>
      <div style="display: flex; flex-wrap: wrap; gap: 10px;">
        <a-card
            v-for="(connection, index) in connections"
            :key="index" :title="'Connection ' + (index + 1)" size="small"
            style="width: 300px">
          <p>IP: {{ connection.ip }}</p>
          <p>Port: {{ connection.port }}</p>
          <template #extra>
            <a @click="navigateTo(connection)">Details</a>
          </template>
        </a-card>
      </div>
    </div>

  </div>
  
</template>

<script>
import {reactive, onMounted, defineComponent} from "vue";
import {mapActions, mapState, useStore} from "vuex";
import { useRouter } from "vue-router";
export default defineComponent({
  name: "ConnectVue",
  setup() {
    const store = useStore(); // 使用 Vuex 的 store
    const router = useRouter(); // 路由
    const formState = reactive({
      ip: "127.0.0.1",
      port: 9999,
    });
    const onSubmit = async (values) => {
      if (!values.ip || !values.port) {
        console.error("Missing IP or Port!");
        return;
      }
      await store.dispatch("connectRemoteServer", values);
    };
    const onFinishFailed = (errorInfo) => {
      console.error("Validation Failed:", errorInfo);
    };
    const navigateTo = (connection) => {
      router.push({ name: "Details", params: { ip: connection.ip, port: connection.port } });
    };
    // 初始化 WebSocket 连接
    onMounted(() => {
      store.dispatch("initConnect");
    });
    return {
      formState,
      onSubmit,
      onFinishFailed,
      navigateTo,
    };
  },
  computed: {
    ...mapState(["connections"]),
  },
  methods: {
    ...mapActions(["connectRemoteServer", "initConnect"]),
  },
});
</script>

<style scoped>
/* Add spacing for cards */
a-card {
  margin-bottom: 10px;
}
</style>
